<template>
  <div id="app">
    <Affixer class="first-title">Meer-Components-Example</Affixer>
    <Liner />
    <ButtonerWrap />
    <Rowcoler />
    <BreadcrumberWrap />
    <DropdownWrap />
    <MenuWrap />
  </div>
</template>

<script>
import ButtonerWrap from './components/Buttoner.vue'
import Rowcoler from './components/Rowcoler.vue'
import BreadcrumberWrap from './components/BreadcrumberWrap.vue'
import DropdownWrap from './components/DropdownWrap.vue'
import MenuWrap from './components/MenuWrap.vue'
export default {
  name: 'app',
  components: {
    ButtonerWrap,
    Rowcoler,
    BreadcrumberWrap,
    DropdownWrap,
    MenuWrap
  }
}
</script>

<style src="./App.css"></style>



