import VueRouter from 'vue-router'
import App from './App.vue'
// cannot use template - vue warning
const Foo = {
    name: 'Foo',
    render(h) {
        return h('div', 'foo')
    }
}
const Bar = {
    name: 'Bar',
    render(h) {
        return h('div', 'bar')
    }
}
const Deep = {
    name: 'Deep',
    render(h) {
        return h('div', 'deep')
    }
}
export default new VueRouter({
    routes: [
        {path: '/foo', component: Foo},
        {path: '/bar', component: Bar},
        {path: '/d/de/dee/deep', component: Deep}
    ]
})


