export default {
    name: 'MeerTitle',
    props: {
        tag: {
            type: String,
            default: 'div'
        },
        title: {
            type: String,
            default: 'meer-title'
        },
        titles: {
            type: Array,
            default: ['meer-title']
        },
        titleClass: {
            type: String,
            default: 'second-title'
        },
        lineWith: {
            type: String,
            default: '50%'
        }
    },
    render(h) {
        const renderTitles = this.titles.map(title => {
            return h('fieldset', [
                h('legend', [title]),
                h('div', [this.$slots[title]])
            ])
        })
        return h(
            this.tag,
            [
                h('p', {class: this.titleClass}, [this.title]),
                h('Liner', {attrs: {width: this.lineWith}})
            ].concat(renderTitles)
        )
    }
}


