<template>
    <meer-title title="Dropdowner" :titles="titles">
        <div slot="Dropdowner-click">
            <Dropdowner title="Dropdowner-click">
                <div>item-one</div>
                <div>item-two</div>
                <div>item-three</div>
            </Dropdowner>
        </div>
        <div slot="Dropdowner-hover">
            <Dropdowner title="Dropdowner-hover" action="hover">
                <div>item-four</div>
                <div>item-five</div>
                <div>item-six</div>
            </Dropdowner>
        </div>
    </meer-title>
</template>
<script>
    export default {
        name: 'DropdownWrap',
        data() {
            return {
                titles: ['Dropdowner-click', 'Dropdowner-hover']
            }
        }
    }
</script>
<style scoped>
    
</style>


