import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
import Meer from '../../../dist/packages/meer.development.js'
Vue.use(Meer)
import App from './App.vue'
import router from './router'
Vue.config.productionTip = false
import MeerTitle from './components/common/MeerTitle'
Vue.component('meer-title', MeerTitle)
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')



