<template>
  <meer-title title="Buttoner" :titles="titles">
    <div :slot="titles[0]">
      <Buttoner text="normal" type="normal" />
      <Buttoner text="simple" type="simple" />
      <Buttoner text="dotted" type="dotted" />
      <Buttoner text="danger" type="danger" />
    </div>
    <div :slot="titles[1]">
      <Buttoner text="large" type="normal" size="large" />
      <Buttoner text="default" type="normal" size="default" />
      <Buttoner text="small" type="normal" size="small" />
    </div>
    <div :slot="titles[2]">
      <Buttoner text="count down" :second="second" />
    </div>
  </meer-title>
</template>
<script>
export default {
  name: 'ButtonerWrap',
  data() {
    return {
      showCountDown: true,
      second: 10,
      titles: ['type', 'size', 'count down']
    }
  },
  props: {
    msg: String
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.fix-margin{
  margin-bottom: 20px;
}
</style>



