<template>
    <meer-title title="Menuer" :titles="titles">
        <div :slot="titles[0]" style="height: 100px">
            <Menuer-group>
                <Menuer :click="clickMenuOne" title="Menuer-one-type-click-default">
                    <Menuer-item :click="clickItemOne" title="Menuer-one-item-one" />
                    <Menuer-item path="/foo" title="Menuer-one-item-two" />
                    <Menuer-item path="/bar" title="Menuer-one-item-three" />
                </Menuer>
                <Menuer type="hover" title="Menuer-two-type-hover">
                    <Menuer-item title="Menuer-two-item-one" />
                    <Menuer-item title="Menuer-one-item-two" />
                    <Menuer-item title="Menuer-one-item-three" />
                </Menuer>
                <Menuer disabled="true" title="Menuer-three-type-disabled">
                    <Menuer-item title="Menuer-three-item-one" />
                </Menuer>
            </Menuer-group>
            <router-view />
        </div>
        <div :slot="titles[1]">
            <Menuer-group direction="vertical" style="float: left">
                <Menuer :click="clickMenuOne" title="">
                    <Menuer-item :click="clickItemOne" title="" />
                    <Menuer-item path="/foo" title="" />
                    <Menuer-item path="/bar" title="" />
                </Menuer>
                <Menuer type="hover" title="">
                    <Menuer-item title="" />
                </Menuer>
                <Menuer disabled="true" title="">
                    <Menuer-item title="" />
                </Menuer>
            </Menuer-group>
            <router-view style="float: left" />
        </div>
    </meer-title>
</template>
<script>
    export default {
        name: 'MenuWrap',
        data() {
            return {
                titles: [
                    'Menuer and Menuer.item, the direction of it is horizon',
                    'Menuer and Menuer.item, the direction of it is vertical'
                ]
            }
        },
        methods: {
            clickItemOne(e) {
                console.log('click item one, the event is:', e)
            },
            clickMenuOne(e) {
                console.log('clicl menu one, the event is:', e)
            }
        }
    }
</script>
<style scoped>
    
</style>


