<template>
  <meer-title title="Rowcoler" :titles="titles">
    <div slot="row-col">
        <Rower>
          <Coler span="6" class="colbg bg1">span-6</Coler>
          <Coler span="6" class="colbg bg2">span-6</Coler>
          <Coler span="6" class="colbg bg3">span-6</Coler>
          <Coler span="6" class="colbg bg4">span-6</Coler>
        </Rower>
        <Rower>
          <Coler span="24" class="fix-margin"><Liner width="100%"/></Coler>        
        </Rower>
        <Rower>
          <Coler span="5" offset="1" class="colbg bg1">span-5-offset-1</Coler>
          <Coler span="15" offset="3" class="colbg bg2">span-15-offset-3</Coler>
        </Rower>
    </div>
  </meer-title>
</template>

<script>
export default {
  name: 'ButtonerWrap',
  data() {
    return {
      titles: ['row-col']
    }
  }
}
</script>

<style scoped>
.colbg{
    height: 30px;
    font-size: 15px;
    color: white;
    font-weight: 500;
}
.bg1{
    background: #b5d8e6;
}
.bg2{
    background: #32b6eb;
}
.bg3{
    background: #b5d8e6;
}
.bg4{
    background: #32b6eb;
}
.fix-margin{
    margin-top: 10px;
    margin-bottom: 10px;
}
</style>



